import React from 'react';
import * as $ from 'axios';
import Header from './component/header/Header';
import Launcher from './component/modal/Launcher';
import './App.css';

class App extends React.Component {

  state = {
    author: '',
    title: '',
    body: '',
    blogList: null
  }

  handleChange = (event) => {
    event.preventDefault();
    this.setState({ 
    [event.target.name]: event.target.value }, function(){console.log(this.state)})
  }

  handleSubmit = (event) => {
    event.preventDefault();
    $.post('/api/posts', { author: this.state.author, title: this.state.title, content: this.state.body })
      .then((result) => {
        this.getPosts();
        this.handleReset();
      })
  }

  handleReset = () => {
    this.setState({
      author: '',
      title: '',
      body: ''
    });
  };
  
  handleDelete = (id) => {
    $.delete(`/api/posts/${id}`)
      .then((result) => {
        this.getPosts();
      })
  }

  getPosts = () => {
    $.get('/api/posts')
      .then((result) => {
        this.setState({ blogList: result.data })
      });
  }

  componentDidMount() {
    this.getPosts();
  }

  render() {
    return (
      <div className="App">
      <Header/>
      <div>
      {
        this.state.blogList ? 
            this.state.blogList.map(ele =>  {
              return(<div>
                <div className="insertedTitles"> {ele.title}</div>
                <div className="displayPic"> <span className="insertedAuthors">by {ele.author} on Jan 22</span></div>
                <div className="insertedBlogs"> {ele.content} </div>
                <hr></hr>
              </div>)
            })
            :
            ""
      }
    </div>
      <Launcher/>
      
  </div>
    );
  }
}

export default App;